import React from "react";
import "./App.css";

class GetResults extends React.Component {
  constructor(props) {
    super(props)
  }
  render() {
    return (
      <div id="results">
        <p id="results-title">YOUR RESULTS</p>
        <div id="results-content">
        {this.props.data != null && <p>Your Skin Anomaly is likely to be {this.props.data.name}.
        Chances of {this.props.data.cancer} cancer are present.</p>}
        </div>
      </div>
    );
  }
}

export default GetResults;
